/* logo properties: */
.user-logo {
  width: 60px;
  height: auto;
  margin: 6px;
}

.top-container {
  display: inline-flex;
  justify-content: center;
  margin: 10px;
}

.title-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 15px;
}

.page-title {
  font-weight: 200;
  font-size: 2.6rem;
}

.first-half-title {
  color: #ff8e53;
}

.second-half-title {
  color: #fe6b8b;
}

.credentials-section-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.follow-message-section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.follow-message {
  font-size: 1.3rem;
  font-weight: 200;
  text-align: center;
  /* colorized font style: */
  background: -webkit-linear-gradient(45deg, #fe6b8b, #ff8e53);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  font-family: comfortaa;
}

.btn-style {
  background-image: linear-gradient(to right, #ff8e53, #fe6b8b);
  border: 0px;
  width: 120px;
  height: auto;
}

.btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.no-account-message > p {
  margin: 0px;
  font-size: 1.1rem;
  color: rgb(87, 87, 87);
  font-family: comfortaa;
  text-align: center;
}

.no-account-message,
.register-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 15px;
}

.register-nav {
  margin-top: 60px;
  margin-bottom: 60px;
  border: 0px;
  padding: 10px;
}

.register-nav-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.other-msg {
  height: 25vh;
}

/* import fonts from google fonts platform: */

/* set footer background: */
.footer-container {
  background-image: linear-gradient(
    to right,
    rgb(187, 187, 187),
    #ff8e53,
    #fe6b8b
  );
  margin: 0;
}

.contact-logo {
  width: 50px;
}

.contact-platform-name {
  font-size: 1rem;
  color: whitesmoke;
  margin: 10px;
  text-decoration: none;
  cursor: pointer;
}

.contact-platform-name:hover {
  color: #fe6b8b;
  text-decoration: none;
}

.name,
.date-created {
  color: white;
  font-size: 1.15rem;
  margin-top: 5px;
  margin-bottom: 5px;
  cursor: pointer;
}

.color-text {
  color: whitesmoke;
}

.contact-me {
  /* font-family: indie flower; */
  color: whitesmoke;
  font-size: 1.15rem;
  margin-top: 5px;
  margin-bottom: 5px;
  cursor: pointer;
}

.contact-item {
  margin: 10px;
}

.contact-details {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(87, 87, 87);
}

.author-details {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* properties from other file: */
ul {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  margin: 10px;
}

.nav-link:hover {
  color: whitesmoke;
}

li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-item {
  margin-left: 10px;
  margin-right: 10px;
  width: auto;
}

@media all and (max-width: 800px) {
  .contact-logo {
    width: 30px;
    margin: 0px;
  }

  .name,
  .date-created {
    color: white;
    font-size: 15px;
  }

  .contact-me {
    /* font-family: indie flower; */
    color: whitesmoke;
    font-size: 14px;
  }

  .contact-platform-name {
    font-size: 1rem;
    color: whitesmoke;
    margin: 10px;
    text-decoration: none;
  }

  .contact-item {
    margin: 0px;
  }

  .contact-details {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .contact-details > ul {
    display: flex;
    flex-direction: column;
  }

