    font-size: 1.2rem;
    font-family: comfortaa;
  }

  .hobby-img {
    width: 55px;
  }

  .title-page {
    font-weight: 200;
    font-size: 1.4rem;
  }

  .first-half,
  .second-half {
    font-size: 1.2rem;
    font-family: montserrat;
  }

  .page-logo {
    width: 2.2em;
    margin: 0px;
  }

