  overflow: auto;
}

.message-box-and-send-btn {
  width: 50vw;
  height: auto;
  display: flex;
  flex-direction: row;
  /* justify-content: space-around; */
  align-items: center;
  margin: 5px;
}

.message-btn-style {
  border-radius: 1em;
  width: 150px;
  background-color: rgb(47, 47, 47);
}

.message-text-field {
  width: 100%;
}

.message-box {
  margin-right: 20px;
  width: 70%;
}

/* message body text style: */
.message-body-container-sender {
  padding: 2px;
  display: flex;
  justify-content: flex-end;
}

.message-style-sender {
  border-radius: 30px;
  padding: 8px;
  color: whitesmoke;
  font-size: 1.2rem;
  margin: 2px;
  display: inline-block;
  /* background-color: #1178ee; */
  background-color: #ff543d;
  padding-left: 10px;
  padding-right: 10px;

  margin-right: 20px;
  font-weight: 200;
}

.message-body-container-receiver {
  padding: 2px;
  display: flex;
  justify-content: flex-start;
}

.message-style-receiver {
  border-radius: 30px;
  padding: 8px;
  color: whitesmoke;
  font-size: 1.2rem;
  margin: 2px;
  display: inline-block;
  background-color: #5f5f5f;
  /* background-color: #202020; */
  padding-left: 10px;
  padding-right: 10px;

  margin-left: 20px;
  font-weight: 200;
}

.chat-form {
  display: flex;
  flex-direction: row;
}

.message-text-area {
  margin: 1em;
  border: 1px solid gray;
  border-radius: 1em;
  width: 100%;
  font-size: 1rem;
}

.messages-form-style {
  display: flex;
  justify-content: center;
  padding: 1em;

  width: 50%;
}

.messages-form-style > .message-btn-style {
  margin: 1em;
}

.message-box-and-send-btn {
  height: auto;
}

/* responsive section: */
@media (max-width: 800px) {
  .message-page-main-container {
    width: 95vw;
    height: 100vh;
  }

  .user-message-picture,
  .name-and-close-btn {
    margin-left: 10px;
    margin-right: 10px;
  }

  .message-style-receiver {
    margin-left: 2px;
  }

  .message-style-sender {
    margin-right: 2px;
  }

