h2 {
  font-weight: 200;
  text-align: center;
}

.matches-list-title {
  padding: 25px;
}

.user-picture-section {
  padding: 10px;
}

.profile-picture {
  width: 110px;
  height: auto;
  border-radius: 20px;
}

h4 {
  font-weight: 200;
}

.user-card-container {
  display: flex;
  flex-direction: row;
}

.fname-lname {
  display: flex;
  flex-direction: row;
}

.username {
  padding: 5px;
  color: whitesmoke;
  /* color: #ff8e53; */
}

.hobbies-city {
  /*that has already default position:  */
  display: flex;
  flex-direction: column;
}

.hobbies-description {
  display: flex;
  flex-direction: column;
}

.user-card-container {
  border: 0px solid gray;
  padding: 30px;
  margin: 20px;
  background-color: rgb(87, 87, 87);
  box-shadow: black 8px 8px 10px;
}

.user-data-section {
  display: flex;
  flex-direction: column;
  margin: 15px;
}

.user-hobby-text {
  padding-left: 5px;
  padding-right: 5px;
}

.hobbies-description,
.city-container {
  margin: 0px;
}

h5 {
  padding-left: 5px;
  padding-right: 5px;
  font-weight: 200;
}

.city-text {
  /* color:rgb(255, 208, 0); */
  color: #fdc4a5;
  font-weight: 300;
}

.user-hobby-text,
h5,
.fname,
.lname,
.user-rating {
  color: whitesmoke;
}

.city-container {
  display: flex;
  flex-direction: row;
}

.user-rating {
  padding-left: 15px;
  padding-right: 5px;
}

/* user matched hobby item style: */
.matched-user-hobbies {
  display: flex;
  flex-direction: column;
}

.matched-user-hobbies > h5 {
  margin: 0;
}

.matched-user-hobbies > ul {
  display: flex;
  flex-direction: column;
  padding: 1px;
}

.hobby-list-item {
  color: #ff8e53;
  margin: 0;
}

@media all and (max-width: 800px) {
  .user-card-container {
    display: flex;
    flex-direction: column;
  }
}
